*,
*::after,
*::before {
  box-sizing: border-box;
  font-size: inherit;
  font-family: inherit;
  margin: 0;
  padding: 0;
}

body {
  overflow-y: scroll;
  scrollbar-width: "none";
  -ms-overflow-style: "none";
}
body::-webkit-scrollbar {
  display: none;
}
body {
  padding: 30px;
}